import React, { useState, useEffect, useContext } from "react";
import { Fade } from "react-reveal";
import emoji from "react-easy-emoji";
import { greeting } from "../../portfolio";
import SocialMedia from "../../components/socialMedia/SocialMedia";
import Button from "../../components/button/Button";
import PdfViewer from "../../components/pdfViewer/PdfViewer";
import StyleContext from "../../contexts/StyleContext";
import pdf from "../../assets/documents/CurriculumVitae_ChiChung_Au.pdf"
import sectionImg from "../../assets/images/manOnTable.svg"
import "./Greeting.css";

export default function Greeting() {
	const [showPdf, setShowPdf] = useState(false);
	const [isOnline, setIsOnline] = useState(navigator.onLine);
	const [isMobile, setIsMobile] = useState(false);

	const { isDark } = useContext(StyleContext);

	const handleDownload = (pdf) => {
        window.open(pdf, '_blank');
    }

	const handleMobileScreen = () => {
		const screenWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
		console.log("screenWidth: " + screenWidth);
		const mobileScreenWidth = 415;
		const isMobile = screenWidth < mobileScreenWidth;
		setIsMobile(isMobile);
	};

	useEffect(() => {
		const handleOnlineStatus = () => {
			setIsOnline(true);
		};

		const handleOfflineStatus = () => {
			setIsOnline(false);
		};

		handleMobileScreen();

		window.addEventListener('online', handleOnlineStatus);
		window.addEventListener('offline', handleOfflineStatus);
		window.addEventListener('resize', handleMobileScreen);

		return () => {
			window.removeEventListener('online', handleOnlineStatus);
			window.removeEventListener('offline', handleOfflineStatus);
			window.removeEventListener('resize', handleMobileScreen);
		}
	}, []);

	return (
		<Fade bottom duration={1000} distance="40px">
			<div className="greet-main" id="greeting">
				<div className="greeting-main">
					<div className="greeting-text-div">
						<div>
							<h1 className={isDark ? "dark-mode greeting-text" : "greeting-text"}>
								{" "}
								{greeting.title}{" "}
								<span className="wave-emoji">{emoji("")}</span>
							</h1>

							<p className={isDark ? "dark-mode greeting-text-p" : "greeting-text-p subTitle"}>
								{greeting.subTitle}
							</p>

							{ isOnline ? <SocialMedia /> : "" }

							<div className="button-greeting-div">
								<Button text="Contact Info" href="#contact" />
								<PdfViewer pdf={pdf} onCancel={()=>setShowPdf(false)} open={showPdf} />
								{
									// v1.0: resume in Google Drive
									// <Button text="Resume" newTab={true} href={greeting.resumeLink} />
									isMobile ? <Button text="Resume" onClick={()=>handleDownload(pdf)} /> : <Button text="Resume" onClick={()=>setShowPdf(!showPdf)} />
								}
							</div>
						</div>
					</div>

					<div className="greeting-image-div">
						<img alt="Alvin sitting on table" src={sectionImg} />
					</div>
				</div>
			</div>
		</Fade>
	);
}
