import React, { useState, useContext } from "react";
import { Modal, Button, Divider } from "antd";
import { ZoomInOutlined, ZoomOutOutlined, DownloadOutlined } from '@ant-design/icons';
import PDF from "react-pdf-js";
import StyleContext from "../../contexts/StyleContext";
import "./PdfViewer.css";

const PdfViewer = ({ pdf, onCancel, open }) => {
    const [page, setPage] = useState(1);
    const [pages, setPages] = useState(null);
    const [scale, setScale] = useState(1);

    const { isDark } = useContext(StyleContext);

    const onDocumentComplete = (numPages) => {
        setPages(numPages);
    }

    const onDocumentError = (err) => {
        console.log("PDF viewer error: ", err);
    }

    const onSetScale = (type) => {
        var newScale = type ? scale + 0.1 : (scale >= 0.8 ? scale - 0.1 : scale);
        if (newScale > 2) {
            newScale = 2;
        } else if (newScale < 0.1) {
            newScale = 0.1;
        }
        setScale(newScale);
    }

    const onPage = (type) => {
        var newPage = type ? page + 1 : page - 1;
        if (newPage > pages) {
            newPage = 1;
        } else if (newPage < 1) {
            newPage = pages;
        }
        setPage(newPage);
    }

    const handleDownload = (pdf) => {
        window.open(pdf, '_blank');
    }

    const customStyle = {
        marginLeft: 10,
        marginRight: 10,
        cursor: 'pointer'
    }

    const footer = <div className="footer">
        <Button onClick={() => onPage(0)}>Previous</Button>
        <div>
            <span style={{textAlign: 'center'}}>Page { page } of { pages }</span>
            <Divider className={isDark ? "dark-divider" : ""} type="vertical" />
            <ZoomOutOutlined style={{...customStyle, opacity: scale === 0.1 ? 0.5 : 1}} onClick={() => onSetScale(0)} />
            <span>{ Math.round(scale * 100) }%</span>
            <ZoomInOutlined style={{...customStyle, opacity: scale === 2 ? 0.5 : 1}} onClick={() => onSetScale(1)} />
            <Divider className={isDark ? "dark-divider" : ""} type="vertical" />
            <DownloadOutlined style={{...customStyle}} onClick={() => handleDownload(pdf)} />
        </div>
        <Button onClick={() => onPage(1)}>Next</Button>
    </div>

    return (
        <Modal
            className={isDark ? "dark-mode custom-modal" : "custom-modal"}
            onCancel={onCancel}
            open={open}
            width={"100%"}
            wrapClassName={isDark ? "dark-modal" : ""}
            footer={footer}>

            <div className="pdfWrapper">
                <PDF
                    file={pdf}
                    onDocumentComplete={onDocumentComplete}
                    onDocumentError={onDocumentError}
                    page={page}
                    scale={scale}
                />
            </div>
        </Modal>
    );
};
export default PdfViewer;