import React, { Component } from "react";
import Header from "../components/header/Header";
import Greeting from "./greeting/Greeting";
import Skill from "./skill/Skill";
import SkillProgress from "./skillProgress/SkillProgress";
import WorkExperience from "./workExperience/WorkExperience";
import Project from "./project/Project";
// import StartupProject from "./startupProject/StartupProject";
import Achievement from "./achievement/Achievement";
import Blog from "./blog/Blog";
// import Contact from "./contact/Contact";
import Footer from "../components/footer/Footer";
// import Talk from "./talk/Talk";
// import Podcast from "./podcast/Podcast";
import Education from "./education/Education";
import TopButton from "./topButton/TopButton";
// import Twitter from "./twitter-embed/Twitter";
import Profile from "./profile/Profile";
// import { educationInfo } from "../portfolio";
import { StyleProvider } from "../contexts/StyleContext";
import "./Main.css";


export default class Main extends Component {
	constructor(props) {
		super(props);
		this.state = {
			isDark: false,
		};
	}

	componentDidMount() {
		window.addEventListener('online', this.handleNetworkChange);
		window.addEventListener('offline', this.handleNetworkChange);
		window.addEventListener("resize", this.resize.bind(this));
    	this.resize();
		// const darkPref = window.matchMedia("(prefers-color-scheme: dark)");
		//this.setState({ isDark: darkPref.matches });
		this.setState({ isDark: true }); /* default dark-mode */
	}

	componentWillUnmount() {
		window.removeEventListener('online', this.handleNetworkChange);
		window.removeEventListener('offline', this.handleNetworkChange);
		window.removeEventListener("resize", this.resize.bind(this));
	}

	resize() {
		let currentHideNav = (window.innerWidth <= 760);
		if (currentHideNav !== this.state.hideNav) {
			this.setState({hideNav: currentHideNav});
		}
	}

	handleNetworkChange = () => {
		this.setState({ isOnline: navigator.onLine });
	}

	changeTheme = () => {
		this.setState({ isDark: !this.state.isDark });
	};

	render() {
		return (
			// Original render
			/*
			<div className={this.state.isDark ? "dark-mode" : null}>
				<StyleProvider value={{ isDark: this.state.isDark, changeTheme: this.changeTheme }}>
					<Header />
					<Greeting />
					<Skill />
					<SkillProgress />
					<Education />
					<WorkExperience />
					<Project />
					<StartupProject />
					<Achievement />
					<Blog />
					<Talk />
					<Twitter />
					<Podcast />
					<Profile />
					<Footer />
					<TopButton />
				</StyleProvider>
			</div>
			*/

			<div className={this.state.isDark ? "dark-mode" : null}>
				<StyleProvider value={{ isDark: this.state.isDark, changeTheme: this.changeTheme }}>
					<Header />
					<Greeting />
					<Skill />
					<SkillProgress />
					<Education />
					<WorkExperience />
					<Project />
					<Achievement />
					<Blog />
					<Profile />
					<Footer />
					<TopButton />
				</StyleProvider>
			</div>
		);
	}
}
