import React from "react";
import { skillsSection } from "../../portfolio";
import "./SoftwareSkill.css";

export default function SoftwareSkill() {
	return (
		<div>
			<div className="software-skills-main-div">
				<ul className="dev-icons">
					{skillsSection.softwareSkills.map((softwareSkill) => {
						return (
							<li key={softwareSkill.id} className="software-skill-inline" name={softwareSkill.skillName}>
								<i className={softwareSkill.fontAwesomeClassname}></i>
								<p>{softwareSkill.skillName}</p>
							</li>
						);
					})}
				</ul>
			</div>
		</div>
	);
}
