import React, { useState, useEffect, lazy, Suspense } from "react";
import axios from "axios";
import { openSource } from "../../portfolio";
import Contact from "../contact/Contact";
import Loading from "../loading/Loading";

const renderLoader = () => <Loading />;
const GithubProfileCard = lazy(() => import('../../components/githubProfileCard/GithubProfileCard'));

export default function Profile() {
	const [prof, setrepo] = useState([]);
	const [isOnline, setIsOnline] = useState(navigator.onLine);

	function setProfileFunction(array) {
		setrepo(array);
	}

	function getProfileData() {
		if (isOnline) {
			console.log("getProfileData() INFO: INTERNET CONNECTED");
			axios.get("https://api.github.com/users/" + openSource.githubUserName)
			.then(function(response) {
				setProfileFunction(response.data);
			})
			.catch(function(error) {
				console.log(error);
				setProfileFunction("Error");
				console.log("Because of this error contact section is showed instead of profile");
				openSource.showGithubProfile = "false";
			});
		} else {
			console.log("getProfileData() ERROR: INTERNET DISCONNECTED");
		}
	}

	useEffect(() => {
		const handleOnlineStatus = () => {
			setIsOnline(true);
		};

		const handleOfflineStatus = () => {
			setIsOnline(false);
		};

		window.addEventListener('online', handleOnlineStatus);
		window.addEventListener('offline', handleOfflineStatus);

		if (openSource.showGithubProfile === "true") {
			getProfileData();
		}

		return () => {
			window.removeEventListener('online', handleOnlineStatus);
			window.removeEventListener('offline', handleOfflineStatus);
		}
		// eslint-disable-next-line
	}, []);

	if (openSource.showGithubProfile === "true" && !(typeof prof === 'string' || prof instanceof String)) {
		return (
			<Suspense fallback={renderLoader()}>
				<GithubProfileCard prof={prof} key={prof.id} />
			</Suspense>
		);
	} else {
		return (
			<Contact />
		);
	}
}
