import React, { useState, useEffect, useContext, Suspense, lazy } from "react";
import axios from "axios";
import Button from "../../components/button/Button";
import Loading from "../loading/Loading";
import { openSource, socialMediaLinks } from "../../portfolio";
import { StyleConsumer } from "../../contexts/StyleContext";
import "./Project.css";

export default function Project() {
	const GithubRepoCard = lazy(() => import("../../components/githubRepoCard/GithubRepoCard"));
	const FailedLoading = () => null;
	const renderLoader = () => <Loading />;
	const [repo, setrepo] = useState([]);
	const [isOnline, setIsOnline] = useState(navigator.onLine);
	const { isDark } = useContext(StyleConsumer);

	function setrepoFunction(array) {
		setrepo(array);
	}

	function getRepoData() {
		// const client = axios.get("https://gh-pinned-repos.egoist.dev/?username=" + openSource.githubUserName)
		if (isOnline) {
			console.log("getRepoData() INFO: INTERNET CONNECTED");
			axios.get("https://github-pinned-repo-api.cyclic.app/?username=" + openSource.githubUserName)
			.then(function(response) {
				setrepoFunction(response.data);
			})
			.catch(function(error) {
				console.log(error);
				setrepoFunction("Error");
				console.log(
					"Because of this error, nothing is shown in place of projects section. projects section not configured"
				);
			});
		} else {
			console.log("getRepoData() ERROR: INTERNET DISCONNECTED");
		}
	}

	useEffect(() => {
		const handleOnlineStatus = () => {
			setIsOnline(true);
		};

		const handleOfflineStatus = () => {
			setIsOnline(false);
		};

		window.addEventListener('online', handleOnlineStatus);
		window.addEventListener('offline', handleOfflineStatus);

		getRepoData();

		return () => {
			window.removeEventListener('online', handleOnlineStatus);
			window.removeEventListener('offline', handleOfflineStatus);
		}
		// eslint-disable-next-line
	}, []);
	
	if (!(typeof repo === "string" || repo instanceof String) && isOnline) {
		return (
			<Suspense fallback={renderLoader()}>
				<div className="main" id="opensource">
					<h1 className="project-title">Open Source Projects</h1>

					<div className="repo-cards-div-main">
						{repo.map((item, index) => {
							return (
								<div key={index}>
									<GithubRepoCard repo={item} isDark={isDark} />
								</div>
							);
						})}
					</div>
					
					<Button text={"More Projects"} className="project-button" href={socialMediaLinks.github} newTab={true} />
				</div>
			</Suspense>
		);
	} else {
		return <FailedLoading />;
	}
}
